.header {
  background: #FE75F0;
  height: 54px;
  position: relative;
}
.header--active {
  z-index: 1000;
}
.header__inner {
  height: 54px;
  padding: 7px 16px;
  background: #FE75F0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.header__burger {
  width: 40px;
  height: 40px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.header__burger-open {
  display: block;
}
.header__burger-close {
  display: none;
}
.header__burger--active .header__burger-open {
  display: none;
}
.header__burger--active .header__burger-close {
  display: block;
}
.header__nav {
  width: 100%;
  height: 115px;
  padding: 20px 0;
  position: fixed;
  z-index: 1200;
  top: 54px;
  left: 0;
  transform: translateX(-100%);
  background: #FE75F0;
  overflow-y: auto;
  transition: transform 0.3s ease;
}
.header__nav--active {
  transform: translateX(0);
}
.header__nav-close {
  display: inline-block;
  position: absolute;
  right: 16px;
  top: 16px;
  width: 40px;
  height: 40px;
}
.header__list {
  padding-left: 30px;
  padding-bottom: 20px;
  display: flex;
  overflow-x: auto;
}
.header__link {
  padding: 15px 20px;
  display: inline-flex;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 81%;
  font-family: PixelDigivolveCyrillic;
  color: #663EC6;
  border: 5px solid transparent;
  transition: color 0.2s ease;
  white-space: nowrap;
}
.header__link:hover, .header__link:focus {
  color: #43FE01;
}
.header__item {
  margin-bottom: 10px;
}
.header__item--active .header__link {
  border: 5px solid #000;
  color: #000;
}

@media screen and (min-width: 1280px) {
  .header {
    height: 80px;
  }
  .header__inner {
    width: 100%;
    max-width: 100%;
    height: 80px;
    padding: 12px 16px;
  }
  .header__burger {
    display: none;
  }
  .header__nav {
    height: auto;
    padding: 0;
    position: static;
    transform: translateX(0);
    overflow: visible;
  }
  .header__nav-close {
    display: none;
  }
  .header__list {
    width: 100%;
    max-width: 1240px;
    padding: 0 20px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header__item {
    margin-bottom: 0;
    position: relative;
  }
  .header__item::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -30px;
    transform: translateY(-50%);
    width: 15px;
    height: 12px;
    background: url("../images/icons/link-icon.svg");
  }
  .header__item:last-child::after {
    display: none;
  }
  .header__item--active::after {
    display: none;
  }
  .header__item--active .header__link {
    padding: 10px 24px;
  }
  .header__link {
    padding: 0;
    font-size: 24px;
  }
}
.main {
